import Event from "@/common/tools/watcher.js"
import tableRow from "@/components/table-row.vue"
export default {
	components: {
		tableRow
	},
	props: {
		columns: {
			type: Array,
			required: true
		},
		list: {
			type: Array,
			required: true
		},
		rowClassName:{
			type:[String,Function],
			default:""
		},
		'slot-cols':{
			type:Array,
			default:()=>{
				return []
			}
		},
		"td-width": {
			type: Number,
			default: 220
		},
		"td-height": {
			type: Number,
			default: 160
		},
		"td-padding": {
			type: Number,
			default: 10
		},
		"border-color": {
			type: String,
			default: "#666"
		}
	},

	created() {
		//给下级传递参数
		//申请参数响应的时间，必须把接收的事件传递过来
		Event.$on("column-get-table-attr", (e) => {
			let t = {
				"td-width": this.tdWidth,
				"td-height": this.tdHeight,
				"td-padding": this.tdPadding,
				"border-color": this.borderColor
			};
			Event.$emit(e, t);
		});
		console.log(this.slotCols.indexOf('Name')>-1)

	},
	methods:{
		rowClassNamePlus(row,index){
			if(typeof this.rowClassName==="string"){
					return this.rowClassName;
			}
			else if(typeof this.rowClassName==="function"){
				return this.rowClassName(row,index);
			}
		},
		pullEvent(event,data){
				console.log("触发时间")
				console.log(event)
				console.log(data)
				this.$emit(event,data);
		}
	}
}
